import React, { Suspense } from "react";
import { Redirect } from "react-router-dom";
import { Route, Switch } from "react-router-dom";
import Layout from "./components/layout/Layout";
import LoadingSpinner from "./components/UI/LoadingSpinner";

const AllQuotes = React.lazy(() => import("./pages/AllQuotes"));
const NewQuote = React.lazy(() => import("./pages/NewQuote"));
const NotFound = React.lazy(() => import("./pages/NotFound"));
const QuoteDetails = React.lazy(() => import("./pages/QuoteDetail"));

function App() {
  return (
    <Layout>
      <Suspense
        fallback={
          <div className="centered">
            <LoadingSpinner />
          </div>
        }
      >
        <Switch>
          <Route path="/quotes-app/" exact>
            <Redirect to="/quotes-app/quotes" />
          </Route>
          <Route path="/quotes-app/quotes" exact>
            <AllQuotes />
          </Route>
          <Route path="/quotes-app/quotes/:quoteID">
            <QuoteDetails />
          </Route>
          <Route path="/quotes-app/new-quote">
            <NewQuote />
          </Route>
          <Route path="/quotes-app/*">
            <NotFound />
          </Route>
        </Switch>
      </Suspense>
    </Layout>
  );
}

export default App;
